@import url(https://fonts.googleapis.com/css?family=Noto+Serif);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    font-family: 'Noto Serif', serif;
    height: 100vh;
}
.header {
    height: 100px;
    background: rgb(41, 198, 112);
    display: flex;
    align-items: center;
    padding: 10px;
}
.header__img {
    height: 100%;
    width: 100px;
}
.header__img img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.header__content {
    padding: 0 10px;
    height: 100%;
}
.header__content h2 {
    font-size: 1.3rem;
}
.tab {
    position: fixed;
    z-index: 4;
    height: 50px;
    bottom: 0;
    left: 0;
    display: flex;
    width: 100%;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.tab > div {
    width: 25%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
}
.tab > div:active {
    -webkit-transform: scaleY(2);
            transform: scaleY(2);
}
.tab > div > span {
    height: 30px;
    width: 30px;
    background: white;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.tab > div > span.active {
    color: white;
    font-weight: 500;
    background: rgb(15, 13, 13);
}

.tab > div:nth-child(1) {
    background: rgb(202, 78, 78);
}
.tab > div:nth-child(2) {
    background: rgb(115, 255, 0);
}
.tab > div:nth-child(3) {
    background: rgb(18, 144, 218);
}
.tab > div:nth-child(4) {
    background: rgb(42, 6, 99);
}
.section {
    height: calc(100vh - 150px);
    background: white;
}
.section__header {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: white;
    transition: background-color 1s;
}
.section__content {
    height: calc(100% - 50px);
    overflow: auto;
}
.content-row {
    padding-left: 40px;
    padding-top: 10px;
    margin-bottom: 20px;
    position: relative;
}
.content-row p {
    font-style: italic;
    margin-top: 10px;
}
.cirle {
    height: 25px;
    width: 25px;
    border: 1px solid;
    border-radius: 50%;
    position: absolute;
    z-index: 2;
    top: 10px;
    left: 5px;
    background: #7cfd0a;
}
.line {
    position: absolute;
    z-index: 1;
    width: 3px;
    border: 1px solid;
    top: 30px;
    left: 16px;
    bottom: -30px;
}
.content-row__imgs {
    /* height: 100px; */
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-start;
}
.content-row__imgs img {
    height: auto;
    width: 80%;
    /* object-fit: contain; */
    margin: 10px 0;
}

